import React from "react"
import Container from "react-bootstrap/Container"

import { schedule } from "content/Schedule"
import { TextBlock } from "components/Text Block"
import { Item } from "components/Item"

const Schedule = ({ loginDetails }) => {

    const scheduleItems = schedule

    const isInvitedToEvent = (event) => {
        return loginDetails.invitation.events.includes(event);
    }

    return (
        <>

            <TextBlock
                text="Event schedule coming soon!">
            </TextBlock>
            <br></br>
            <Container fluid>
                {scheduleItems.map(
                    (
                        {
                            id,
                            Title,
                            Date,
                            Time,
                            Location,
                            Summary,
                            Details,
                        },
                        idx
                    ) => (isInvitedToEvent(id) &&
                        <Item
                            key={idx}
                            LeftTitle={Title}
                            LeftFirstSubtitle={Date}
                            LeftSecondSubtitle={Time}
                            LeftThirdSubtitle={Location}
                            RightBody={Summary}
                            HiddenBody={Details}
                        />
                       
                    )
                )}
            </Container>
        </>
    )
}

export default Schedule
